import React, { useEffect, forwardRef, useState, useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom"
import styles from "../styles/modules/Header.module.css";
import Icon from "./Icon";
import { useDarkMode, useOverlay, useThemeColor } from '..';
import { MdOutlineCheck, MdClose } from "react-icons/md";
import { CgDetailsMore } from "react-icons/cg";
import { BiLinkExternal } from "react-icons/bi";
import { MdLightMode, MdDarkMode } from "react-icons/md";

import ToggleSwitch from './ToggleSwitch';
import DropdownMenu from './DropdownMenu';
import ColorWheel from './ColorWheel';

import Liquid from "./Liquid";

import { projectDataArray } from "../../data/projectData";
import { BsBack } from "react-icons/bs";
import { IoReturnUpBackOutline } from "react-icons/io5";
import { FaArrowTurnUp } from "react-icons/fa6";

const Header = ({ collapsedHeader }, ref) => {
    const navigate = useNavigate()
    const location = useLocation()
    const [activeTab, setActiveTab] = useState(location.pathname.slice(1))
    const { primaryColor, setPrimaryColor, secondaryColor, setSecondaryColor } = useThemeColor();
    const { isDarkMode, toggleDarkMode } = useDarkMode();
    const projectOverlayRef = useRef()
    const [loading, setLoading] = useState(true)
    const [isAutoScrolling, setIsAutoScrolling] = useState(autoScrollOnOpen || false);
    const [autoScrollSpeed, setAutoScrollSpeed] = useState(10)
    const [settingsDropdown, setSettingsDropdown] = useState(false)
    const scrollIntervalId = useRef();
    const containerRef = useRef()
    const [end, setEnd] = useState(false)
    const { overlay, setOverlay, autoScrollOnOpen, setAutoScrollOnOpen, expandedHeader, setExpandedHeader } = useOverlay()
    const controlsRef = useRef()

    const roles = [
        "Fullstack web developer",
        "Graphic designer",
        "Code monkey",
    ]
    let lastPosition;

    const debounce = (func, delay) => {
        let timeoutId;
        return function (...args) {
            clearTimeout(timeoutId);
            timeoutId = setTimeout(() => func(...args), delay);
        };
    };

    let timer
    let timer2
    const navigateTo = (destination) => {
        clearTimeout(timer)
        const mainElement = document.querySelector("main");
        mainElement.classList.add("fadeOut");
        setExpandedHeader(false);
        timer = setTimeout(() => {
            clearTimeout(timer2)
            mainElement.classList.remove("fadeOut");
            navigate(destination);
            setOverlay()
            mainElement.classList.add("fadeIn");
            timer2 = setTimeout(() => {
                mainElement.classList.remove("fadeIn");
            }, 250)
        }, 250)
    }

    const handleRangeChange = debounce((e) => {
        setAutoScrollSpeed(70 - e.target.value)
        stopTimer()
    }, 300);

    const handleNavigateAndOpenWindow = (link) => {
        window.open(link, '_blank');
    };

    const setTimer = () => {
        const scrollStep = 1;
        if (containerRef.current) {
            scrollIntervalId.current = setInterval(() => {
                const currentPosition = containerRef.current.scrollY || containerRef.current.scrollTop;

                if (containerRef.current.scrollHeight <= containerRef.current.clientHeight) {
                    clearInterval(scrollIntervalId.current);
                    setIsAutoScrolling(false);
                    return;
                }

                if (currentPosition === lastPosition) {
                    clearInterval(scrollIntervalId.current);
                    setIsAutoScrolling(false);
                    setEnd(true)
                    return;
                } else {
                    setIsAutoScrolling(true);
                    setEnd(false)
                }

                lastPosition = currentPosition;
                containerRef.current.scroll(0, currentPosition + scrollStep);
            }, autoScrollSpeed);
        }
    }

    const stopTimer = () => {
        if (isAutoScrolling) {
            clearInterval(scrollIntervalId.current);
            setIsAutoScrolling(false);
            setTimer()
            setEnd(false)
        }
    }

    const toggleAutoScroll = () => {
        if (!isAutoScrolling) {
            setTimer()
            setEnd(false)
        } else {
            clearInterval(scrollIntervalId.current);
            setIsAutoScrolling(false);
            setEnd(false)
        }
    };

    useEffect(() => {
        const handleInterupt = (e) => {
            if (isAutoScrolling) {
                if ((e.type == "keydown" || e.type == "mousedown") && (controlsRef.current && !controlsRef.current.contains(e.target))) {
                    clearInterval(scrollIntervalId.current);
                    setIsAutoScrolling(false);
                }
                if (e.type == "wheel" || e.type == "scroll") {
                    clearInterval(scrollIntervalId.current);
                    setIsAutoScrolling(false);
                }
            }
            setEnd(false)
        };

        window.addEventListener("mousedown", handleInterupt);
        window.addEventListener("keydown", handleInterupt);
        window.addEventListener("wheel", handleInterupt);
        window.addEventListener("scroll", handleInterupt);

        return () => {
            window.removeEventListener("mousedown", handleInterupt);
            window.removeEventListener("keydown", handleInterupt);
            window.removeEventListener("wheel", handleInterupt);
            window.removeEventListener("scroll", handleInterupt);
        };
    }, [isAutoScrolling]);

    useEffect(() => {
        return () => {
            clearInterval(scrollIntervalId.current);
        };
    }, [overlay]);

    useEffect(() => {
        autoScrollOnOpen && !isAutoScrolling && toggleAutoScroll()
    }, [overlay, loading])

    const RollingWord = ({ className }) => {

        return (
            <div className={`${styles.rollingWordsContainer} ${className ? className : ""}`}>
                <div className={`${styles.rollingWords}`}>
                    {roles.map((role) => <i className={`${collapsedHeader ? "invisible" : "visible"}`}>{role}</i>)}
                </div>
            </div>
        )

    }

    useEffect(() => {
        const wordsArray = location.pathname.slice(1).split('/');
        const firstWord = wordsArray[0];
        setActiveTab(firstWord)
    }, [location])
    const [isResizing, setIsResizing] = useState(false);

    const handleMouseDown = (e) => {
        e.stopPropagation()
        setIsResizing(true);
    };

    const handleMouseUp = (e) => {
        setIsResizing(false);
        
    };

    const handleMouseMove = (e) => {
        if (isResizing && collapsedHeader) {
            const newHeight = e.clientY;
            ref.current.style.height = `${newHeight + 5}px`;
        }
    };

    useEffect(() => {
        const handleScroll = () => {
            ref.current.classList.add("transitionUnset")
            const scrollPosition = window.innerHeight - window.scrollY;
            const newHeight = scrollPosition;
            if (!collapsedHeader && newHeight >= window.innerWidth * 0.1) {
                ref.current.style.height = `${newHeight}px`;
            } else {
                ref.current.style.height = `${window.innerWidth * 0.1}px`;
            }
        };

        const handleScrollEnd = () =>{
            ref.current.classList.remove("transitionUnset")
        }

        document.addEventListener("scroll", handleScroll);
        document.addEventListener("scrollend", handleScrollEnd);
        window.addEventListener("resize", handleScroll);

        return () => {
            document.removeEventListener("scroll", handleScroll);
            document.removeEventListener("scrollend", handleScrollEnd);
            window.removeEventListener("resize", handleScroll);
        };
    }, [collapsedHeader]);

    useEffect(() => {
        if (collapsedHeader) {
            ref.current.style.height = `${window.innerWidth * 0.1}px`;
        } else {
            const scrollPosition = window.innerHeight - window.scrollY;
            const newHeight = scrollPosition;
            if (!collapsedHeader && newHeight >= window.innerWidth * 0.1) {
                ref.current.style.height = `${newHeight}px`;
            }
        }
    }, [collapsedHeader, location]);

    useEffect(() => {
        document.addEventListener("mousemove", handleMouseMove);
        document.addEventListener("mouseup", handleMouseUp);

        return () => {
            document.removeEventListener("mousemove", handleMouseMove);
            document.removeEventListener("mouseup", handleMouseUp);
        };
    }, [isResizing]);


    return (
        <div ref={ref} className={`${styles.container} ${expandedHeader? styles.expanded : ""}`} style={{ height: `${collapsedHeader && window.innerWidth * 0.1}px` }}>
            <Liquid bgOn={!collapsedHeader} follows={true} />
            {projectDataArray.map((project, i) => {
                return overlay == project.name && (<>
                    <h1>
                        {project.name}
                    </h1>
                    <div className={styles.projectOverlay} ref={projectOverlayRef}>
                        <div className={styles.controls} ref={controlsRef}>
                            <button aria-label="Project details" onClick={() => {
                                navigateTo(`/projects/${project.name}`)
                            }}>
                                <CgDetailsMore />
                            </button>
                            <button aria-label="Open hosted" onClick={() => handleNavigateAndOpenWindow(project.hostedAt)}>
                                <BiLinkExternal />
                            </button>
                            <button aria-label="Close" onClick={() => {
                                setExpandedHeader(false)
                                setOverlay("")
                            }}>
                                <FaArrowTurnUp />
                            </button>
                        </div>
                        {project.preview.type == "image" &&
                            <img key={i} onClick={() => navigateTo(`/projects/${project.name}`)} src={project.preview.url} />
                        }
                        {project.preview.type == "video" &&
                            <video controlsList="nodownload" loading="lazy" key={i} onClick={() => navigateTo(`/projects/${project.name}`)} muted autoPlay={false} playsinline loop id="myVideo">
                                <source src={project.preview.url} type="video/mp4" />
                                Your browser does not support the video tag.
                            </video>
                        }
                        {project.assets && project.assets.length > 0 && project.assets.map((asset, i) => {
                            if (asset.type == "video") {
                                return (
                                    <video controlsList="nodownload" loading="lazy" key={i + "asset"} onClick={() => navigateTo(`/projects/${project.name}`)} playsinline autoPlay={false} muted loop id="myVideo">
                                        <source src={asset.url} type="video/mp4" />
                                        Your browser does not support the video tag.
                                    </video>
                                )
                            } else if (asset.type == "image") {
                                return <img key={i + "asset"} src={asset.url} onClick={() => navigateTo(`/projects/${project.name}`)} />
                            }
                        })}
                    </div>
                </>)
            })}
            <h1>
                <b className={`${collapsedHeader ? styles.invisible : ""}`}>Hi, my name is</b>
                {!overlay && "Benjamin Chick"}
                <RollingWord className={`${collapsedHeader ? styles.invisible : ""}`} />
            </h1>
            <nav>
                <ul>
                    <li aria-label='Home' label-position="left" className={!activeTab ? styles.activeTab : ""}>
                        <button onClick={() => {
                            navigateTo("/")
                        }}>
                            <Icon name={"MdHomeFilled"} />
                        </button>
                    </li>
                    <li aria-label='Projects' label-position="left" className={(activeTab == "all" || activeTab == "projects" || activeTab == "live" || activeTab == "sideProjects") ? styles.activeTab : ""}>
                        <button onClick={() => {
                            navigateTo("/all")
                        }}>
                            <Icon name={"FaCode"} />
                        </button>
                    </li>
                    <li aria-label='Contact' label-position="left" className={activeTab == "contact" ? styles.activeTab : ""}>
                        <button onClick={() => {
                            navigateTo("/contact")
                        }}>
                            <Icon name={"MdEmail"} />
                        </button>
                    </li>
                    <li>
                        <button onClick={() => {
                            setSettingsDropdown(prev => !prev)
                        }}>
                            <Icon name={"IoMdSettings"} />
                        </button>
                        <DropdownMenu active={settingsDropdown} setActive={setSettingsDropdown}>
                            <span>
                                <ToggleSwitch id={"darkMode"} isChecked={isDarkMode} onChange={toggleDarkMode} checkedIcon={<MdLightMode />} unCheckedIcon={<MdDarkMode />} />
                            </span>
                            <span>
                                <ColorWheel color={primaryColor} setColor={setPrimaryColor} />
                                <ColorWheel color={secondaryColor} setColor={setSecondaryColor} />
                            </span>
                            <span>
                                <div>
                                    Toggle autoscroll
                                    <ToggleSwitch
                                        id={"toggleAutoScroll"}
                                        isChecked={isAutoScrolling}
                                        onChange={() => {
                                            setSettingsDropdown(false)
                                            toggleAutoScroll()
                                        }}
                                        checkedIcon={<MdOutlineCheck />}
                                        unCheckedIcon={<MdClose />} />
                                </div>
                                <div>
                                    Scroll on open
                                    <ToggleSwitch
                                        id={"autoScrollOnOpenSwitch"}
                                        isChecked={autoScrollOnOpen}
                                        onChange={() => setAutoScrollOnOpen(prev => !prev)}
                                        checkedIcon={<MdOutlineCheck />}
                                        unCheckedIcon={<MdClose />} />
                                </div>
                                <div>
                                    Scroll speed
                                    <input type="range" min={1} max={70} defaultValue={70 - autoScrollSpeed} onChange={handleRangeChange} />
                                </div>
                            </span>
                        </DropdownMenu>
                    </li>
                </ul>
            </nav>
        </div>
    );
};

export default forwardRef(Header);
