import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import styles from "../styles/modules/IndexPage.module.css";
import ScrollToTopButton from "../components/ScrollToTopButton"
import ScrollDownIndicator from "../components/ScrollDownIndicator";

import Carousel from "../components/Carousel";
import { FaReact, FaHtml5, FaCss3Alt, FaGithub, FaNodeJs, FaGit, FaGithubAlt, FaLinkedin, FaCodepen } from "react-icons/fa";
import { SiOpenai, SiBootstrap, SiFigma, SiVite, SiGimp, SiAdobephotoshop, SiAdobeillustrator, SiWebpack, SiBabel, SiMongodb, SiFirebase, SiExpress, SiJest } from "react-icons/si";
import RippleButton from "../components/RippleButton";
import { GiCoffeeMug } from "react-icons/gi";

import westSuffolkCouncilSvg from "../../public/assets/westSuffolkLogo.svg"
import DNModaSVG from "../../public/assets/DNModa.svg"
import AFCSVG from "../../public/assets/AFCLogo.svg"
import KFCSVG from "../../public/assets/KFCLogo.svg"

import placeholder from "../../public/assets/keanu.webp"

const IndexPage = () => {
	const [isThanking, setIsThanking] = useState(false)
	const navigate = useNavigate()

	let timer1;
	const navigateTo = (destination, delay, openBlank) => {
		clearTimeout(timer1)
		timer1 = setTimeout(() => {
			if(openBlank){
				window.open(destination, '_blank');
			}else{
				navigate(destination)
			}
		}, (delay || 10))
	}

	let timer2
	const thank = () => {
		setIsThanking(true)
		navigateTo('https://www.paypal.com/donate/?business=MVVJWUBMGV866&no_recurring=0&currency_code=GBP', 2000, true);
		clearTimeout(timer2)
		timer2 = setTimeout(() => {
			setIsThanking(false)
		}, 2500)
	}

	useEffect(() => {
		const handleScroll = () => {
			const sections = document.querySelectorAll("section")
			sections.length > 0 && sections.forEach((section) => {
				if (window.scrollY >= section.offsetTop - (window.innerHeight * 0.75)) {
					section.style.opacity = "1"
				} else {
					section.style.opacity = "0"
				}
			})
		}

		document.addEventListener("scroll", handleScroll)
		return () => {
			document.removeEventListener("scroll", handleScroll)
		}
	})

	return (
		<div className={styles.pageContainer}>
			<ScrollDownIndicator />
			<ScrollToTopButton />
			<section className={styles.section}>
				<img src={placeholder} />
				<p>
					I'm a web designer passionate about seamless and visually compelling online experiences. <Link to={"/projects"}>Here</Link> you will find a collection of works I have done throughout the years.
				</p>
			</section>
			<section className={styles.section}>
				<h2>
					"The final product<br />exceeded our expectations, and the <b>attention to detail</b> was exceptional."
				</h2>
				<p><i>- <b>Anna</b>, DN Moda</i></p>
			</section>
			<section className={styles.section}>
				<div className={styles.affiliations}>
					<img src={westSuffolkCouncilSvg} />
					<img src={AFCSVG} />
					<img src={KFCSVG} />
					<img src={DNModaSVG} />
				</div>
				<p>
					I have worked with many different clients on many different projects creating web apps, eCommerce, games etc.
				</p>
			</section>
			<section>
				<Carousel>
					<div>
						<FaReact />
					</div>
					<div>
						<FaHtml5 />
					</div>
					<div>
						<FaCss3Alt />
					</div>
					<div>
						<FaGithub />
					</div>
					<div>
						<FaNodeJs />
					</div>
					<div>
						<FaGit />
					</div>
					<div>
						<SiOpenai />
					</div>
					<div>
						<SiBootstrap />
					</div>
					<div>
						<SiFigma />
					</div>
					<div>
						<SiVite />
					</div>
					<div>
						<SiGimp />
					</div>
					<div>
						<SiAdobephotoshop />
					</div>
					<div>
						<SiAdobeillustrator />
					</div>
					<div>
						<SiWebpack />
					</div>
					<div>
						<SiBabel />
					</div>
					<div>
						<SiMongodb />
					</div>
					<div>
						<SiFirebase />
					</div>
					<div>
						<SiExpress />
					</div>
					<div>
						<SiJest />
					</div>
				</Carousel>
				<p>With my years of experience in design and your great idea, we can make something truely one of a kind.</p>
			</section>
			<section>
				<div className={`${styles.thanks} ${isThanking ? "isVisible" : "invisible"}`}>
					<span>
						🥳
						<span>T</span>
						<span>h</span>
						<span>a</span>
						<span>n</span>
						<span>k</span>
						<span>s</span>
						<span>!</span>
						🎉
					</span>
				</div>
				<h3 className={`${isThanking ? "invisible" : "visible"}`}>
					Collaborate with me
				</h3>
				<p className={`${isThanking ? "invisible" : "visible"}`}>
					If you are interested in collaborating on a project or have any inquiries regarding my services, I would love to hear from you.
				</p>
				<span>
					<RippleButton alt={true} onClick={() => navigateTo("/contact")} className={`${!isThanking ? "isVisible" : "invisible"}`}>
						Get in touch
					</RippleButton>
					<RippleButton ripple={false} className={`${styles.buyCoffee} ${isThanking ? styles.thankClick : ""}`} celebrate={true} onClick={thank}>
						Buy me a <GiCoffeeMug />
					</RippleButton>
					<RippleButton alt={true} onClick={() => navigateTo("/all")} className={`${!isThanking ? "isVisible" : "invisible"}`}>
						View Gallery
					</RippleButton>
				</span>
			</section>
		</div>
	);
};

export default IndexPage;