px) {\n    .searchResults {\n        position: absolute;\n    }\n}\n\n.container section{\n    display: flex;\n    justify-content: center;\n    height: calc(100vh - 10vw);\n}\n\n.container section nav,\n.container section nav ul,\n.container section nav ul li {\n    display: flex;\n    height: 100% !important;\n    width: 100% !important;\n    overflow: hidden;\n    flex-direction: column;\n}\n\n.container section nav ul li:not(:first-of-type) {\n    margin-left: 15px;\n}\n\n.container section nav ul li a:first-of-type {\n    margin-left: 0 !important;\n}\n\n.container section nav ul li a {\n    margin-left: 15px !important;\n}\n\n.container section nav ul a:hover {\n    color: var(--secondary-accent-color) !important;\n}\n\n.container section a {\n    display: flex;\n    opacity: 80%;\n    white-space: nowrap;\n    transition: all 0.2s ease-in-out;\n    color: var(--primary-text-color);\n}\n\n.container .activeTab {\n    color: var(--primary-background-color-alpha) !important;\n}\n\n.container .inactiveTab a {\n    color: var(--primary-text-color) !important;\n}\n\n.filteredContent {\n    display: flex;\n    flex-direction: row;\n    align-content: baseline;\n    justify-content: baseline;\n    position: relative;\n    flex-wrap: wrap;\n    width: 100%;\n    overflow-y: scroll;\n    gap: 6px;\n    padding: 3px;\n    box-sizing: border-box;\n}\n\n.filteredContentControls{\n    position: fixed;\n    top: 10vw;\n    left: 0;\n    height: 100%;\n    z-index: 9;\n}\n\n.container button{\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background: none !important;\n    box-shadow: none !important;\n    outline: none !important;\n    margin: 0 !important;\n    padding: 5px !important;\n}\n\n.container button svg{\n    color: var(--primary-text-color);\n}\n\n.card {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-direction: column;\n    height: 150px;\n    width: 150px !important;\n    background: var(--primary-background-color);\n    position: relative;\n    overflow: hidden;\n    z-index: 2;\n    cursor: pointer;\n    outline: 2px solid transparent;\n    box-sizing: border-box;\n    user-select: none;\n    outline: 3px solid var(--background-color);\n}\n\n@keyframes fadeIn {\n    from{\n        opacity: 0;\n    }\n    to{\n        opacity: 1;\n    }\n}\n\n.card h4 {\n    font-size: 2rem;\n    line-height: 3rem;\n    user-select: none;\n}\n\n.card:hover {\n    outline: 5px solid var(--primary-accent-color);\n    z-index: 9;\n}\n\n.container section .card video {\n    position: absolute;\n    object-fit: cover;\n    object-position: center;\n    height: 100%;\n    width: 100%;\n    opacity: 1;\n    transition: all 0.2s ease-in-out;\n    user-select: none;\n}\n\n.container section .card img {\n    object-fit: cover !important;\n    object-position: center;\n    height: 100%;\n    width: 100%;\n    transition: all 0.2s ease-in-out;\n    user-select: none;\n}\n\n.card p {\n    position: absolute;\n    top: 0%;\n    padding: 5px 10px;\n    color: white !important;\n    font-weight: 600;\n    height: 100%;\n    width: 100%;\n    max-width: 100vw;\n    overflow: hidden;\n    opacity: 0%;\n    z-index: 2;\n}\n\n.card:hover p {\n    opacity: 100%;\n    user-select: none;\n}\n\n.searchContainer {\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    overflow: visible !important;\n    position: relative;\n    animation: expand 0.2s forwards linear;\n}\n\n@keyframes expand {\n    from{\n        max-height: 0;\n    }\n    to{\n        max-height: 35px;\n    }\n}\n\n\n.searchContainer input{\n    border: 0;\n    height: 35px;\n    width: 100% !important;\n    padding: 15px;\n    background: none;\n    color: var(--primary-text-color);\n    z-index: 1;\n}\n\n.searchContainer input::placeholder{\n    color: var(--primary-text-color);\n    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n}\n\n.searchContainer::after{\n    position: absolute;\n    content: \"\";\n    bottom: 0;\n    left: 0;\n    display: flex;\n    width: 100%;\n    height: 2px;\n    background: linear-gradient(90deg,transparent, var(--primary-accent-color)5%, var(--secondary-accent-color), transparent);\n}\n\n.searchResults,\n.searchResults ul,\n.searchResults ul li,\n.searchResults ul li a {\n    width: 100% !important;\n    gap: 0 !important;\n    font-size: small;\n    margin: 0 !important;\n    overflow: visible !important;\n    color: var(--primary-text-color);\n    background: none;\n}\n\n.searchResults ul li{\n    position: relative;\n    cursor: pointer;\n    height: 35px;\n    display: flex;\n    align-items: center;\n}\n\n.searchResults ul li a{\n    z-index: 1;\n}\n\n.searchResults ul li:hover::after{\n    content: \"\";\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background: var(--gradient2);\n}\n\n.searchResults {\n    bottom: 200px;\n    left: 0;\n    color: var(--primary-text-color);\n    width: calc(100% - 45px);\n    display: flex;\n    justify-content: center;\n    flex-direction: column;\n    background: var(--primary-background-color);\n    overflow: visible !important;\n    z-index: 0;\n}\n\n.searchResults::after{\n    content: \"\";\n    position: absolute;\n    top: 35px;\n    left: 0;\n    display: flex;\n    height: 1px;\n    width: 100%;\n    background: linear-gradient(90deg, var(--border-color) 5%, transparent);\n    z-index: 1;\n}\n\n.listView {\n    color: var(--primary-text-color);\n    font-family: 'Courier New', Courier, monospace;\n}\n\n.listView p {\n    width: 100%;\n    padding: 0px 0px 0px 15px;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n\n.listCols p {\n    color: var(--primary-text-color) !important;\n    font-weight: 600 !important;\n}\n\n.listView>div {\n    height: min-content;\n    width: 100% !important;\n}\n\n.listView>div:hover {\n    color: var(--background-color) !important;\n    background: var(--gradient2);\n    cursor: pointer;\n}\n\n.listItem {\n    display: flex;\n}\n\n.galleryView{\n    position: relative;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    height: calc(100vh - 10vw - 15px) !important;\n    max-width: calc(100vw - 25px) !important;\n    flex-wrap: nowrap;\n    overflow: hidden !important;\n    gap: 0;\n    padding: 25px;\n}\n\n.galleryControls > button{\n    position: fixed;\n    width: 35px;\n    top: 50vh;\n    z-index: 1;\n}\n\n.galleryView button svg{\n    color: var(--primary-text-color);\n    height: 30px;\n    width: 30px;\n}\n\n.galleryView button:hover{\n    transition: 0.2s all ease-in-out;\n    transform: scale(130%);\n}\n\n.galleryView button:hover svg{\n    transition: 0.2s all ease-in-out;\n    color: var(--primary-accent-color);\n}\n\n.galleryControls > button:nth-child(1){\n    left: 20px;\n}\n\n.galleryControls > button:nth-child(2){\n    right: -5px;\n}\n\n.galleryCard{\n    display: flex;\n    align-items: center;\n    height: 60vh;\n    filter: brightness(10%);\n    z-index: -1 !important;\n    overflow: hidden;\n}\n\n.spotLight{\n    height: 100vh;\n    width: 85vw;\n    border-radius: 0.5rem;\n    max-height: calc(100vh - 10vw - 150px) !important;\n    border: 10px solid white;\n    overflow: visible !important;\n    filter: brightness(100%);\n    background: var(--background-color);\n}\n\n@media screen and (max-width: 800px) {\n    .galleryView{\n        flex-direction: column;\n        max-height: 100vh;\n        width: 100vw;\n    }\n    .galleryCard{\n        height: 100%;\n        width: 80vw;\n        max-height: 15px;\n    }\n    .spotLight{\n        width: 85vw;\n    }\n}\n\n.spotLight::after{\n    content: \"\";\n    position: absolute;\n    left: 50%;\n    bottom: -15%;\n    transform: translateX(-50%);\n    height: 100px;\n    border-radius: 50%;\n    width: 150%;\n    background: radial-gradient(rgba(0, 0, 0, 0.7), transparent 70%);\n    z-index: 1;\n}\n\n.spotLight::before{\n    content: \"\";\n    position: absolute;\n    left: 0%;\n    bottom: 0%;\n    height: 100%;\n    width: 100%;\n    z-index: 1;\n    opacity: 50%;\n    box-shadow: inset 0 50px 100px black;\n}\n\n.galleryCard img,\n.galleryCard video{\n    object-position: center;\n    object-fit: cover;\n    width: 100%;\n    height: 100%;\n}"],"sourceRoot":""}]);
