import React, { useEffect, useState, useRef } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { projectDataArray } from "../../data/projectData";

import styles from "../styles/modules/ProjectsPage.module.css";

import ResizableDiv from "../components/ResizableDiv";
import { useOverlay } from "..";
import { FaChevronLeft, FaChevronRight, FaFilter, FaSearch } from "react-icons/fa";
import { MdViewHeadline, MdViewQuilt } from "react-icons/md";
import { HiRectangleStack } from "react-icons/hi2";

const ProjectsPage = () => {
    const filteredRef = useRef()
    const [view, setView] = useState("tile")
    const [activeFilter, setActiveFilter] = useState();
    const [toggleSearchBar, setToggleSearchBar] = useState(false)
    const [filterActive, setFilterActive] = useState(true)
    const [subCategory, setSubCategory] = useState()
    const [filteredData, setFilteredData] = useState([])
    const location = useLocation();
    const { overlay, setOverlay, expandedHeader, setExpandedHeader } = useOverlay()


    const handleNavMouseOver = (dataRole) => handleNavMouse(dataRole, true);
    const handleNavMouseLeave = (dataRole) => handleNavMouse(dataRole, false);

    const handleNavMouse = (dataRole, isOver) => {
        const elements = document.querySelectorAll(`[data-role="${dataRole}"]`);
        elements.length > 0 && elements.forEach((element) =>
            isOver ? element.classList.add(styles.hovered) : element.classList.remove(styles.hovered)
        );
    };

    const Search = () => {
        const [searchValue, setSearchValue] = useState("");
        const [searchResults, setSearchResults] = useState([]);

        const searchByField = (fieldName) => {
            const lowercaseSearchValue = searchValue;
            return filteredData.filter(item => {
                const lowercaseFieldValue = item[fieldName].toLowerCase();
                return lowercaseFieldValue.includes(lowercaseSearchValue.toLowerCase());
            });
        };

        const handleInput = (e) => {
            if (e.target.value.length > 3) {
                setSearchValue(e.target.value);
            } else {
                setSearchResults([])
            }
        };

        useEffect(() => {
            if (searchValue && searchValue.length > 3) {
                const nameSearch = searchByField("name")
                const categorySearch = searchByField("category")
                const subCategorySearch = searchByField("subCategory")

                // Combine the three arrays
                const combinedArray = [...nameSearch, ...categorySearch, ...subCategorySearch];

                // Use a Set to eliminate duplicates
                const uniqueValues = new Set(combinedArray);

                // Convert the Set back to an array
                const resultArray = Array.from(uniqueValues);

                setSearchResults(resultArray);
            } else {
                setSearchResults([]);
            }
        }, [searchValue]);

        return toggleSearchBar && (
            <div className={styles.searchContainer}>
                <input type="search" placeholder="Search..." onInput={handleInput} style={{ borderRadius: searchResults.length > 0 ? `0.2rem .2rem 0 0` : `0.2rem` }} />
                {searchResults.length > 0 && (
                    <div className={styles.searchResults}>
                        <ul>
                            {searchResults && searchResults.length > 0 && searchResults.map((result, index) => (
                                <li>
                                    <Link key={index} to={`/projects/${result.name}`}>
                                        {result.name}
                                    </Link>
                                </li>
                            ))}
                        </ul>

                    </div>
                )}
            </div>
        );
    };

    useEffect(() => {
        const pathSegments = location.pathname.slice(1).split('/');
        setActiveFilter(pathSegments[0]);

        const subCategory = pathSegments.length > 1 ? pathSegments[1] : undefined;
        setSubCategory(subCategory);

        if (subCategory) {
            const filteredData = projectDataArray.filter(project => project.subCategory == subCategory)
            setFilteredData(filteredData)
        } else {
            setFilteredData(projectDataArray)
        }
    }, [location.pathname]);

    useEffect(() => {
        document.title = "Projects"
        return () => document.title = "Benjamin John Chick"
    }, [location.pathname])

    useEffect(() => {
        document.querySelector("html").style.overflowY = 'hidden';
        window.scrollTo(0, 0)
        return () => {
            document.querySelector("html").style.overflowY = 'visible';
            window.scrollTo(0, 0)
        };
    }, []);

    const handleMenuView = () => {
        const views = [
            "tile",
            "list",
            "gallery"
        ]
        const currentIndex = views.findIndex(val => val == view)
        setView(prev => {
            if (currentIndex == views.length - 1) {
                return views[0]
            } else {
                return views[currentIndex + 1]
            }
        })
    }

    return (
        <div className={styles.container}>
            <section>
                <ResizableDiv view={view} setView={setView} filterActive={filterActive}>
                    <nav>
                        <ul>
                            <li>
                                <Link
                                    className={`${activeFilter == "all" ? styles.activeTab : styles.inactiveTab}`}
                                    to="/all"  >
                                    all
                                </Link>
                            </li>
                            <li
                                className={`${activeFilter == "live" ? styles.activeTab : styles.inactiveTab}`}
                                onMouseOver={(e) => handleNavMouseOver("live")}
                                onMouseLeave={(e) => handleNavMouseLeave("live")}>
                                <Link
                                    className={`${activeFilter == "live" ? styles.activeTab : styles.inactiveTab}`}
                                    to="/live" >
                                    live
                                </Link>
                                {activeFilter == "live" && <Link
                                    className={`${(subCategory == "webDev") ? styles.activeTab : styles.inactiveTab}`}
                                    to="/live/webDev" >
                                    webDevelopment
                                </Link>}
                            </li>
                            <li
                                className={`${activeFilter == "sideProjects" ? styles.activeTab : styles.inactiveTab}`}
                                onMouseOver={(e) => handleNavMouseOver("sideProjects")}
                                onMouseLeave={(e) => handleNavMouseLeave("sideProjects")}>
                                <Link
                                    className={`${activeFilter == "sideProjects" ? styles.activeTab : styles.inactiveTab}`}
                                    to="/sideProjects" >
                                    projects
                                </Link>
                                {activeFilter == "sideProjects" && (
                                    <Link
                                        className={`${subCategory == "webDev" ? styles.activeTab : styles.inactiveTab}`}
                                        to="/sideProjects/webDev" >
                                        webDevelopment
                                    </Link>
                                )}
                            </li>
                            <li
                                className={`${(activeFilter == "design" || activeFilter == "design/logos" || activeFilter == "design/typography") ? styles.activeTab : styles.inactiveTab}`}
                                onMouseOver={(e) => handleNavMouseOver("design")}
                                onMouseLeave={(e) => handleNavMouseLeave("design")}>
                                <Link
                                    className={`${activeFilter == "design" ? styles.activeTab : styles.inactiveTab}`}
                                    to="/design" >
                                    design
                                </Link>
                                {activeFilter == "design" && <Link className={`${subCategory == "logos" ? styles.activeTab : styles.inactiveTab}`} to="/design/logos" >logos</Link>}
                                {activeFilter == "design" && <Link className={`${subCategory == "typography" ? styles.activeTab : styles.inactiveTab}`} to="/design/typography" >typography</Link>}
                                {activeFilter == "design" && <Link className={`${subCategory == "icons" ? styles.activeTab : styles.inactiveTab}`} to="/design/icons" >icons</Link>}
                            </li>
                        </ul>
                    </nav>
                </ResizableDiv>
                <div ref={filteredRef} className={`${styles.filteredContent} ${activeFilter ? styles.expanded : styles.collapsed} ${view == "gallery" ? styles.galleryView : ""} ${view == "list" ? styles.listView : ""}`}
                    onContextMenu={(e) => {
                        e.preventDefault()
                    }}>
                    <Search />
                    {view == "gallery" && <div className={styles.galleryControls}>
                        <button onClick={() => {
                            const index = filteredData.length / 2;
                            if (index !== -1) {
                                const valueToMove = filteredData.splice(index, 1)
                                const leftOrderedData = filteredData.filter((val, i) => (val !== valueToMove) && (i > filteredData.length / 2))
                                const rightOrderedData = filteredData.filter((val, i) => (val !== valueToMove) && (i < filteredData.length / 2))
                                leftOrderedData.push(valueToMove[0])
                                const orderedData = leftOrderedData.concat(rightOrderedData)
                                setFilteredData(orderedData)
                            }
                        }}>
                            <FaChevronLeft />
                        </button>
                        <button onClick={() => {
                            const index = (filteredData.length / 2)+1;
                            if (index !== -1) {
                                const valueToMove = filteredData.splice(index, 1)
                                const leftOrderedData = filteredData.filter((val, i) => (val !== valueToMove) && (i > (filteredData.length+1 / 2)))
                                const rightOrderedData = filteredData.filter((val, i) => (val !== valueToMove) && (i < (filteredData.length+1 / 2)))
                                leftOrderedData.push(valueToMove[0])
                                const orderedData = leftOrderedData.concat(rightOrderedData)
                                setFilteredData(orderedData)
                            }
                        }}>
                            <FaChevronRight />
                        </button>
                    </div>}
                    {filteredData && filteredData.length > 0 && filteredData.map((project, i) => {
                        if (project.preview.type == "video" && (activeFilter == "all" || project.category == activeFilter)) {
                            return (<>
                                <div key={project.name + i} >
                                    <div
                                        key={i} id={"tile" + i}
                                        className={`${view == "gallery" ? styles.galleryCard : ""} ${i == filteredData.length / 2 && view == "gallery" ? styles.spotLight : ""} ${view == "tile" ? styles.card : ""} ${view == "list" ? styles.listItem : ""}`}
                                        data-role={project.category}
                                        data-role2={project.subCategory}
                                        onClick={() => {
                                            setExpandedHeader(true)
                                            setOverlay(project.name)
                                        }}>
                                        {(view == "gallery" || view == "tile") && (
                                            <video
                                                controlsList="nodownload"
                                                loading="lazy"
                                                muted
                                                autoPlay={i == filteredData.length / 2 ? true : false}
                                                controls={i == filteredData.length / 2 ? true : false}
                                                playsInline
                                                loop
                                                id="myVideo">
                                                <source src={project.preview.url} type="video/mp4" />
                                                Your browser does not support the video tag.
                                            </video>
                                        )}
                                    </div>
                                </div>
                            </>)
                        }
                        if (project.preview.type == "image" && (activeFilter == "all" || project.category == activeFilter)) {
                            return (
                                <div key={project.name + i} >
                                    <div
                                        key={i}
                                        id={"tile" + i}
                                        className={`${view == "gallery" ? styles.galleryCard : ""} ${i == filteredData.length / 2 ? styles.spotLight : ""} ${view == "tile" ? styles.card : ""} ${view == "list" ? styles.listItem : ""}`}
                                        data-role={project.category}
                                        data-role2={project.subCategory}
                                        onClick={() => {
                                            setExpandedHeader(true)
                                            setOverlay(project.name)
                                        }}>
                                        {(view == "gallery" || view == "tile") && <img loading="lazy" src={project.preview.url} />}
                                    </div>
                                </div>
                            )
                        }
                    })}
                </div>
                <div className={styles.filteredContentControls}>
                    <button aria-label="Search" label-position="right" onClick={(e) => setToggleSearchBar(prev => !prev)}>
                        <FaSearch />
                    </button>
                    <button aria-label="Sort" label-position="right" onClick={(e) => setFilterActive(prev => !prev)}>
                        <FaFilter />
                    </button>
                    <button aria-label={view.slice(0, 1).toUpperCase() + view.slice(1)} label-position="left" className={`${styles.filterButton} ${styles.filterButton2}`}
                        onClick={handleMenuView}>
                        {view == "tile" && <MdViewQuilt />}
                        {view == "list" && <MdViewHeadline />}
                        {view == "gallery" && <HiRectangleStack />}
                    </button>
                </div>
            </section>
        </div>
    )
}

export default ProjectsPage